<template>
  <div class="purchaseRecords">
    <div class="purchaseRecordsHeader">
      <div class="headerContent">
        <van-icon name="arrow-left" @click="$router.go(-1)" />
        <span>{{ $t("index.text18") }}</span>

        <img
          src="../../assets/img/pic14.png"
          class="icon"
          alt=""
          @click="$router.push('/sellingRecords')"
        />
      </div>
    </div>
    <div class="slot"></div>

    <div class="purchaseRecordsMain">
      <div
        class="rewardItem jcsb"
        v-for="(item, index) in recordsList.orders"
        :key="item.id"
      >
        <div class="left">
          <div class="reward">-{{ fromWei(item.usdtAmount) }}USDT</div>

          <div class="reward1">+{{ fromWei(item.usdtAmount) }}HF</div>
          <div class="reward1">+{{ fromWei(item.HLAmount) }}HL</div>
          <div class="reward1">
            {{ $t("index.text173") }}{{ fromWei(item.newPrice) }}
          </div>
          <div class="tiem">{{ getFullTime(item.investTime) }}</div>
        </div>

        <div
          class="right jcc"
          @click="selling(index, item)"
          v-if="item.HLAmount > 0"
        >
          {{ $t("index.text117") }}
        </div>
        <div class=" jcc" v-else>
          {{ $t("index.text165") }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import moment from "moment";
export default {
  data() {
    return {
      recordsList: [],
      hl: 0
    };
  },
  activated() {
    this.onLoad();
  },
  filters: {
    fromWei(val) {
      return parseFloat(val).toFixed(4);
    }
  },
  methods: {
    getFullTime(timeStamp) {
      const time = moment(timeStamp * 1000).format("YYYY-MM-DD HH:mm:ss");
      return time;
    },

    async selling(index, item) {
      this.$toast.loading({
        duration: 0,
        forbidClick: true,
        message: this.$t("index.text21"),
        className: "loading"
      });

      try {
        let gasPrice = await this.getGasPrice();
        // let hl = await this.core
        //   .hlContract()
        //   .balanceOf(this.userAddress)
        //   .call();
        // let allowance = await this.core
        //   .hlContract()
        //   .allowance(this.userAddress, this.core.coreAddress)
        //   .call();
        // let a = Number(this.fromWei(allowance));
        // let b = Number(this.fromWei(item.HLAmount));
        // if (a < b) {
        //   let gas2 = await this.core
        //     .hlContract()
        //     .approve(this.core.coreAddress, hl)
        //     .estimateGas();
        //   await this.core
        //     .hlContract()
        //     .approve(this.core.coreAddress, hl)
        //     .send({
        //       from: this.userAddress,
        //       gas: gas2 + 5000,
        //       gasPrice: gasPrice
        //     });
        // }

        let gas = await this.core
          .coreContract()
          // .OrdersellHL(this.userAddress, index)
          .sellHL(this.userAddress, index)
          .estimateGas();
        let res = await this.core
          .coreContract()
          // .OrdersellHL(this.userAddress, index)
          .sellHL(this.userAddress, index)
          .send({
            from: this.userAddress,
            gas: gas + 10000,
            gasPrice: gasPrice
          });
        this.onLoad();
        // this.$api
        //   .post("transactionlog/add", {
        //     address: this.userAddress,
        //     HL: this.fromWei(item.HLAmount),
        //     type: 2,
        //     transactionHash: res.transactionHash
        //   })
        //   .then(res => {
        //     this.$toast(this.$t("index.text154"));
        //     this.onLoad();
        //   });
      } catch (error) {
        // this.$toast.clear();
        this.setErrDialog(error);
      }
      this.$toast.clear();
    },

    async onLoad() {
      this.recordsList = await this.core
        .coreContract()
        .getUserOrders(this.userAddress)
        .call();
      console.log("123", this.recordsList);
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.purchaseRecords {
  height: 100%;
  overflow: scroll;
  background: #ffffff;
}
.purchaseRecordsHeader {
  padding: 0 2rem;
  background: #d12c21;
  position: fixed;
  top: 0;
  width: 100%;
  height: 5rem;
  z-index: 99;

  .headerContent {
    display: flex;
    align-items: center;
    position: relative;
    height: 5rem;
  }
  .van-icon {
    font-size: 1.8rem;
    color: #fff;
  }
  .icon {
    position: absolute;
    right: 0;
    width: 2rem;
    height: 2rem;
  }
  span {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #fff;
    font-size: 1.6rem;
  }
}

.purchaseRecordsMain {
  padding: 0 2rem 4rem 2rem;

  .rewardItem {
    height: 16rem;
    border-bottom: 0.1rem solid #d9d9d9;
    padding: 1.6rem 0;
    .left {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      height: 100%;

      .reward {
        font-weight: bold;
        font-size: 1.6rem;
      }
      .reward1 {
        font-weight: bold;
        color: #d12c21;
        font-size: 1.6rem;
      }
      .tiem {
        margin-top: 1rem;
        color: #666666;
      }
    }
    .center {
      font-weight: bold;
      color: #d12c21;
      font-size: 1.6rem;
    }

    .right {
      height: 3.8rem;
      background: linear-gradient(134deg, #d12c21 0%, #f5c96d 100%);
      border-radius: 2rem;
      color: #fff;
      padding: 0 2.2rem;
    }
  }
}
</style>



